            <p>SpaceX lance 52 satellites Starlink depuis une base californienne</p>
            <p>Une fusée SpaceX a transporté 52 satellites Internet Starlink en orbite depuis la Californie tôt samedi.
              Les deux étages Falcon 9 fusée a décollé de l' espace de travail Vandenberg côtière de base à 04h41 et en arc de cercle sur le Pacifique
              Le premier étage du Falcon est revenu et a atterri sur un drone SpaceX dans l'océan. C'était le 11e lancement et reprise de l'étape.
              La deuxième étape s'est poursuivie en orbite et le déploiement des satellites a été confirmé, a déclaré le commentateur du lancement Youmei Zhou au siège de SpaceX à Hawthorne, en Californie.
              Starlink est un système Internet mondial par satellite que SpaceX construit depuis des années pour fournir un accès Internet aux régions mal desservies du monde.</p>
