            <p>L'Hélicoptère #Mars continue d'avancer, d'avancer, d'avancer ! Ingenuity a terminé avec succès son 18e vol, ajoutant 124,3 secondes à son temps total en altitude sur la planète rouge. 
              L'Ingenuity a couvert 754 pieds (230 mètres) de sol tout en naviguant à 5,6 mph (9,0 km/h) pendant le vol, qui a eu lieu mercredi 15 décembre.</p>
