            <p>De minuscules particules de roche recueillies sur l'astéroïde Ryugu font partie des morceaux de matériau les plus primordiaux jamais examinés sur Terre et pourraient nous donner un aperçu des origines du système solaire.</p>
            <br>
            <hr>
            <br>
            <h3>Planètes du système solaire</h3>
            <p>L'ordre des planètes du système solaire, en commençant le plus près du soleil et en allant vers l'extérieur, est le suivant : Mercure , Vénus , Terre , Mars , Jupiter , Saturne , Uranus , Neptune et ensuite la possible Planète Neuf.</p>
            <p>Depuis la découverte de Pluton en 1930, les enfants ont grandi en apprenant que le système solaire compte neuf planètes. Tout a changé à la fin des années 1990 lorsque les astronomes ont commencé à se demander si Pluton était bien une planète. 
              Dans une décision très controversée, l'Union astronomique internationale a finalement décidé en 2006 de désigner Pluton comme une "planète naine ", réduisant la liste des vraies planètes du système solaire à seulement huit. </p>
            <br>
