<template>
    <router-view/>
    <header>
      <BlogSpace />
      <div class="container">
          <ul class="pc-nav">
            <li> <router-link class="active" id="redirection" to="/">Home </router-link></li>
            <li> <router-link class="marginLandR" id="redirection" to="/about">About</router-link></li>
            <li> <router-link id="redirection" to="/article">Article</router-link></li>
            <li>
              <div class="search">
                <div class="icon">
                </div>
                <div class="input">
                  <input type="text" placeholder="Search" id="mysearch">
                </div>
                <span class="clear" @click="deleteSearch();"></span>
              </div>
            </li>
          </ul>
          <button class="hamburger">
            <div class="bar"></div>
          </button>
      </div>
    </header>
    <nav class="mobile-nav">
      <router-link class="active" id="redirection" to="/">Home </router-link>
      <router-link id="redirection" to="/about">About</router-link>
      <router-link id="redirection" to="/article">Article</router-link>
      <div class="search">
        <div class="icon">
        </div>
        <div class="input"></div>
      </div>
    </nav>

    <div class="btn">
      <img src ="@/assets/arrow-up-circle.svg" class="icone">
    </div>
    
</template>

<script>
import BlogSpace from '@/components/BlogSpace.vue'

export default {
  name: 'Home',
  components: {
    BlogSpace
  },
  mounted(){
      let btn = document.querySelector('.btn');

      btn.addEventListener('click', () => {

        window.scrollTo({
          top: 0,
          left:0,
          behavior: "smooth"
        })
      }) 
      
      window.onload = function (){
          window.addEventListener('scroll', function () {
              if(window.pageYOffset > 100){
                document.querySelector("header").classList.add('is-scrolling');
              }else {
                document.querySelector("header").classList.remove('is-scrolling');
              }
          });

          let menu_btn = document.querySelector('.hamburger');
          let mobile_menu = document.querySelector('.mobile-nav');

          menu_btn.addEventListener('click', function (){
              menu_btn.classList.toggle('is-active');
              mobile_menu.classList.toggle('is-active');
          });
          
        }
        window.onclick = function (){
          let icon = document.querySelector('.icon');
          let search = document.querySelector('.search');
          icon.onclick = function (){
              search.classList.toggle('is-active');
          };
        }
      },
      methods: {
          deleteSearch: function() {
            document.getElementById ('mysearch').value = '';
          }
  }
}
</script>

